/* App.css */

/* Global Styles */
body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

/* Header */
.header {
  background-color: #ffffff;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  color: #333333;
}

.navbar {
  display: flex;
  align-items: center;
}

.nav-menu {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
}

.nav-item {
  margin-right: 20px;
}

.nav-link {
  color: #333333;
  text-decoration: none;
}

.auth-buttons .btn {
  margin-left: 10px;
  background-color: #333333;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
}

/* Hero Section */
.hero-section {
  background-color: #FFF;
  padding: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hero-content {
  flex: 1;
}

.hero-content h1 {
  font-size: 36px;
  color: #ef2670;
  margin-bottom: 20px;
}

.hero-content p {
  font-size: 18px;
  color: #666666;
  margin-bottom: 40px;
}

.btn-get-started {
  background-color: #ef2670;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  cursor: pointer;
}

.hero-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-image img {
  max-width: 100%;
  height: auto;
}

/* Features Section */
.features-section {
  padding: 80px;
  text-align: center;
}

.features-section h2 {
  font-size: 36px;
  color: #ef2670;
  margin-bottom: 40px;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 40px;
}

.feature {
  text-align: center;
}

.feature-icon {
  width: 80px;
  height: 80px;
  background-color: #333333;
  color: #ffffff;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  margin-bottom: 20px;
}

.feature h3 {
  font-size: 24px;
  color: #333333;
  margin-bottom: 10px;
}

.feature p {
  font-size: 16px;
  color: #666666;
}

/* Testimonial Section */
.testimonial-section {
  padding: 80px;
  text-align: center;
}

.testimonial-section h2 {
  font-size: 36px;
  color: #333333;
  margin-bottom: 40px;
}

.testimonial-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
}

/* Footer */
.footer {
  background-color: #333333;
  padding: 20px;
  text-align: center;
}

.footer p {
  color: #ffffff;
  font-size: 14px;
  margin: 0;
}



