/* App.css */

/* Testimonial Section */
.testimonial-section {
  padding: 80px;
  text-align: center;
}

.testimonial-section h2 {
  font-size: 36px;
  color: #333333;
  margin-bottom: 40px;
}

.testimonial-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
}

.testimonial-card {
  background-color: #f5f5f5;
  padding: 20px;
  border-radius: 8px;
}

.testimonial-quote {
  font-size: 18px;
  color: #333333;
  margin-bottom: 20px;
}

.testimonial-name {
  font-size: 16px;
  color: #666666;
  font-weight: bold;
}

.testimonial-designation {
  font-size: 14px;
  color: #999999;
  margin-top: 5px;
}


/* App.css */

/* Mobile Styles */
@media screen and (max-width: 768px) {
  /* Header */
  .header {
    padding: 10px;
    flex-direction: column;
  }

  .navbar {
    margin-top: 10px;
    justify-content: center;
  }

  .nav-menu {
    flex-direction: column;
  }

  .nav-item {
    margin-bottom: 10px;
    margin-right: 0;
  }

  /* Hero Section */
  .hero-section {
    padding: 40px;
    flex-direction: column;
  }

  .hero-content {
    margin-bottom: 40px;
  }

  .hero-image {
    flex: none;
    margin-bottom: 40px;
  }

  /* Features Section */
  .features-section {
    padding: 40px;
  }

  .features-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  /* Testimonial Section */
  .testimonial-section {
    padding: 40px;
  }

  .testimonial-grid {
    grid-template-columns: 1fr;
  }
}
