/* header.css */

.header {
    background-color: #FFF;
    color: #ef2670;
    display: flex;
    align-items: center;
    padding: 10px 50px 10px 10px;
  }
  
  .logo img {
    width: 120px;
  }
  
  .navbar {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
  }
  
  .nav-menu {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .nav-item {
    margin-right: 20px;
  }
  
  .nav-link {
    color: #ef2670;
    text-decoration: none;
    font-weight: 600;
    transition: color 0.3s ease;
  }
  
  .nav-link:hover {
    color: #f07232;
  }
  
  .user-profile {
    margin-left: 20px;
  }
  
  .user-profile img {
    width: 40px;
    border-radius: 50%;
  }


.dropdown {
  position: relative;
}

.dropdown-content {
  width: 150px;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #ffffff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 10px;
  border-radius: 4px;
  z-index: 1;
  opacity: 0;
  transform: translateY(-10px);
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.dropdown-link {
  display: block;
  color: #333333;
  text-decoration: none;
  padding: 8px 16px;
  transition: background-color 0.3s ease;
}

.dropdown-link:hover {
  background-color: #f5f5f5;
}

.nav-item.dropdown:hover .dropdown-content {
  display: block;
  opacity: 1;
  transform: translateY(0);
}
