/* button.css */

.login-button {
    display: inline-block;
    padding: 5px 20px;
    border: 1px solid #ef2670;
    border-radius: 10px;
    color: #ef2670;
    background-color: #FFF;
    font-size: 16px;
    font-weight: bold;
    text-decoration: none;
    transition: all 0.3s ease;
  }
  
  .login-button:hover {
    background-color: #FFF;
    color: #ef2670;
  }

  .btn-get-started {
    background-color: #ef2670;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
  }
  